 derivados de cultivos, determinó que la huella hídrica global relacionada con la producción de cultivos en el período 1996-2005 fue de 7404 mil millones de metros cúbicos por año (78 % verde, 12 % azul, 10 % gris). Se calculó una gran huella hídrica total para el trigo (1087), el arroz (992) y el maíz (770) El trigo y el arroz tienen las huellas de agua azul más grandes, y juntos representan el 45 % de la huella de agua azul mundial.")])]),_c('div',{staticClass:"col-auto"},[_c('figure',[_c('img',{staticClass:"m-auto",attrs:{"src":require("@/assets/curso/temas/tema2/8.svg"),"data-aos":"fade-right"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"cajon color-acento-contenido p-4 mt-0 col-lg-10 m-auto",staticStyle:{"background-color":"#F2FBE6"}},[_c('p',{staticClass:"mb-0",attrs:{"data-aos":"fade-up"}},[_vm._v("El anexo a continuación muestra la Estimación de la huella hídrica de la producción agrícola en Colombia, Huella azul anual para el sector agrícola y la Huella verde anual para el sector agrícola.")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"anexo__icono"},[_c('img',{attrs:{"src":require("@/assets/template/icono-pdf.svg"),"alt":"“Estimación de la huella hídrica de la producción agrícola en Colombia”. En esta table se muestran diferentes productos y la cantidad de huella hídrica tanto verde, como azul y gris en m³/año que requiere."}})])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"anexo__texto"},[_c('b',[_vm._v("Anexo. ")]),_vm._v("Estimación Producción Huella Hídrica")])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"cont_2_2",staticStyle:{"position":"relative"}},[_c('div',{staticClass:"titulo-segundo color-acento-contenido",staticStyle:{"position":"relative"}},[_c('h2',{attrs:{"id":"t_2_2"}},[_vm._v("2.2 Hu